.product{
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: flex-end;
          margin: 10px;
          padding: 20px;
          width: 100%;
          max-width: 400%;
          min-width: 100px;
          background-color: white;
          z-index: 1;
}
.product__rating{
          display: flex;
         }
.product > img{
          max-height: 200px;
          width: 100%;
          object-fit: contain;
          margin-bottom: 15px;
}
.product > button{
          background: yellow;
          border: 1px solid;
          margin-top: 10%;
          border-color: #a88734 #9c7e31 #846a29;
          color: #111;
}
.product_price{
          margin-top: 5px;
}
.product_info{
          height: 100px;
          margin-bottom: 15px;
}